@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css);
.transition-group {
  position: relative;
}

.fade-enter {
  position: absolute;
  overflow: hidden;
  opacity: 0;
  transform: translateX(100%) rotateZ(0deg);
}

.fade-enter-active {
  position: absolute;
  overflow: hidden;
  opacity: 1;
  transform: translateX(-50%) rotateZ(0);
  transition: all 1s ease-in;
}

.fade-exit {
  position: absolute;
  overflow: hidden;
  opacity: 1;
  transform: translateX(-50%) rotateZ(0);
}

.fade-exit-active {
  position: absolute;
  overflow: hidden;
  opacity: 0;
  transform: translateX(-100%) rotateZ(0deg);
  transition: all 1s ease-in;
}

html,
body {
  background: #353b48;
  height: 100vh;
  color: white;
  margin: 0;
  padding: 0;
  font-family: "pretendard";
  -ms-scroll-snap-type: proximity;
      scroll-snap-type: proximity;
  -ms-scroll-snap-type: y proximity;
      scroll-snap-type: y proximity;
}

#root {
  scroll-snap-align: start;
  scroll-margin-top: calc(1em + 2.2rem);
}

a {
  color: white;
  text-decoration: none;
}

.selected {
  background: olivedrab !important;
  border-bottom: oldlace 1px solid;
}

